.login{
    width:100%;
    height:100%;
    background-image:url('./images/bg.jpg');
    background-size:100% 100%;
    .login-header{
        display: flex;
        align-items: center;
        height:80 px;
        
        img{
            width: 80px;
            height: 40px;
            margin: 0 15px 0 50px;
        }
        h1{
            font-size:30px;
            color: white;
        }

    }

    .login-content{
        width: 400px;
        height:300px;
        background-color: white;
        margin: 50px auto;
        padding: 20px 60px;
        h2{
            text-align: center;
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 20px;
        }
        .login-form{
            .login-form-button{
                width:100%;
            }
        }

    }
}
@primary-color: #1DA57A;